/**
 * @description 获取当前日期  YY--MM--DD
 * @return {string}
 */
export const getNowFormatDate = () => {
  let date = new Date();
  let seperator1 = '-';
  let year = date.getFullYear();
  let month = date.getMonth() + 1;
  let strDate = date.getDate();
  if (month >= 1 && month <= 9) {
    month = '0' + month;
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = '0' + strDate;
  }
  let currentDate = year + seperator1 + month + seperator1 + strDate;
  return currentDate;
}

/**
 * @description 获取当前日期 回退N个月 YY--MM--DD
 * @param {number} 回退月份数  小于等于12
 * @return {string}
 */
export const getBackFormatDate = (num) => {
  let date = new Date();
  let seperator1 = '-';
  let year = date.getFullYear();
  let month = date.getMonth() + 1 - num;
  let strDate = date.getDate();

  if (month <= 0) {
    month = 12 + month
    year = year - 1
  }

  if (month >= 1 && month <= 9) {
    month = '0' + month;
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = '0' + strDate;
  }
  let currentDate = year + seperator1 + month + seperator1 + strDate;
  return currentDate;
}



