import axios from 'axios'
import router from '@/router'
// 公共路径
const API_PATH = '/proxy-api'
// axios.defaults.baseURL = ''

// http response 拦截器
axios.interceptors.response.use(response => {
  // console.dir('router' + router)
  if (response.data.code !== 'undefined') {
    switch (response.data.code) {
      case '200':
        break
      case '401':
      case '4010':
      case '4011':
      case '4012':
        // 此处应补全操作 返回 登录页
        router.replace({
          path: '/login'
        })
        break
    }
    return response
  } else {
    return response
  }
}, error => {
  return Promise.reject(error.response.data) // 返回接口返回的错误信息
})

// 基于axios封装的函数fetch 用于接口获取数据
export const fetch = (url, params, method) => {
  method = method.toUpperCase()
  url = API_PATH + url
  let requestConfig = {
    method: method || 'GET',
    url: url,
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    transformRequest: [function (data) {
      let ret = ''
      for (let key in data) {
        ret += encodeURIComponent(key) + '=' + encodeURIComponent(data[key]) + '&'
      }
      return ret
    }],
    // `withCredentials` 表示跨域请求时是否需要使用凭证
    withCredentials: true // 开启携带cookie
  }

  if (requestConfig.method === 'PUT' || requestConfig.method === 'POST' || requestConfig.method === 'PATCH') {
    requestConfig.data = params
  } else {
    requestConfig.params = params
  }
  // 如果有token, 加在头部
  if (sessionStorage.token) {
    requestConfig.headers.token = sessionStorage.token
  }

  return new Promise((resolve, reject) => {
    axios(requestConfig)
      .then(response => {
        resolve(response.data)
      }, err => {
        reject(err)
      })
      .catch((error) => {
        reject(error)
      })
  })
}



