/* 公共接口 */
import {fetch} from '@/support'

// 每个接口命名请都以Api结尾
// 合作商登录
export const partnerLoginApi = params => fetch('/channelBao/officePartner/loginByPassword', params, 'POST')
// 获取合作商首页初始化数据
export const partnerInitApi = params => fetch('/channelBao/officePartner/getHomePage', params, 'POST')
// 获取折线图数据
export const getChartsApi = params => fetch('/', params, 'POST')



